.latest-user-interactions-container {
    text-align: left;
}

.latest-user-interactions-container .interactions-header {
    background-color: gainsboro;
    padding: 11px 13px;
    border-radius: 8px;
}

.latest-user-interactions-container .latest-topics {
    /* text-align: left; */
}

.latest-user-interactions-container .latest-topics .topic-ui {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 11px 0px;
    background-color: gainsboro;
    padding: 4px 8px;
    border-radius: 8px;
}

.latest-user-interactions-container .latest-topics:last-child(0) {
    margin-bottom: 0px;
    color: aqua;
}

.latest-user-interactions-container .latest-topics .topic-ui img {
    width: 110px;
    height: 69px;
    border-radius: 50%;
}

.latest-user-interactions-container .latest-topics .topic-ui .mid-section {
    width: 72%;
}

.latest-user-interactions-container .latest-topics .topic-ui .posted-info {
    width: 92px;
}